 /* stylelint-disable */
#rich-example em {
  font-style: italic;
}

#rich-example h1,
#rich-example h2,
#rich-example h3,
#rich-example h4,
#rich-example h5,
#rich-example h6 {
  margin: 10px 0;
  font-family: inherit;
  font-weight: bold;
  line-height: 20px;
  color: inherit;
  text-rendering: optimizeLegibility;
}

#rich-example h1 small,
#rich-example h2 small,
#rich-example h3 small,
#rich-example h4 small,
#rich-example h5 small,
#rich-example h6 small {
  font-weight: normal;
  color: #7b7b7b;
}

#rich-example h1,
#rich-example h2,
#rich-example h3 {
  line-height: 40px;
}

#rich-example h1 {
  font-size: 39px;
}

#rich-example h2 {
  font-size: 31px;
}

#rich-example h3 {
  font-size: 23px;
}

#rich-example h4 {
  font-size: 17px;
}

#rich-example h5 {
  font-size: 14px;
}

#rich-example h6 {
  font-size: 11px;
}

#rich-example h1 small {
  font-size: 24px;
}

#rich-example h2 small {
  font-size: 18px;
}

#rich-example h3 small {
  font-size: 16px;
}

#rich-example h4 small {
  font-size: 14px;
}

#rich-example ul,
#rich-example ol {
  margin: 0 0 10px 25px;
  padding: 0;
}

#rich-example ul ul,
#rich-example ul ol,
#rich-example ol ol,
#rich-example ol ul {
  margin-bottom: 0;
}

#rich-example li {
  line-height: 20px;
}

#rich-example blockquote {
  background-color: inherit;
}

.RichEditor-root {
  background: #fff;
  border: 1px solid #ddd;
  font-family: "Georgia", serif;
  font-size: 14px;
  padding: 15px;
}

.RichEditor-editor {
  border-top: 1px solid #ddd;
  cursor: text;
  font-size: 16px;
  margin-top: 10px;
}

.RichEditor-editor .public-DraftEditorPlaceholder-root,
.RichEditor-editor .public-DraftEditor-content {
  margin: 0 -15px -15px;
  padding: 15px;
}

.RichEditor-editor .public-DraftEditor-content {
  min-height: 100px;
}

.RichEditor-hidePlaceholder .public-DraftEditorPlaceholder-root {
  display: none;
}

.RichEditor-editor .RichEditor-blockquote {
  border-left: 5px solid #eee;
  color: #666;
  font-family: "Hoefler Text", "Georgia", serif;
  font-style: italic;
  margin: 16px 0;
  padding: 10px 20px;
}

.RichEditor-editor .public-DraftStyleDefault-pre {
  background-color: rgba(0, 0, 0, .05);
  font-family: "Inconsolata", "Menlo", "Consolas", monospace;
  font-size: 16px;
  padding: 20px;
}

.RichEditor-controls {
  font-family: "Helvetica", sans-serif;
  font-size: 14px;
  margin-bottom: 5px;
  user-select: none;
}

.RichEditor-styleButton {
  color: #999;
  cursor: pointer;
  margin-right: 16px;
  padding: 2px 0;
  display: inline-block;
}

.RichEditor-activeButton {
  color: #5890ff;
}
/* stylelint-enable */
